.dpit-ab-li {

	&__status {
		color: rgb(var(--dpit-uac-badge-bg));
		font-size: 11px;
		font-weight: 600;
	}

	&__icon {
		line-height: 1 !important;

		svg {
			width: 16px;
			height: 16px;
		}

		&.dashicons {
			font-family: dashicons !important;
			font-size: 18px !important;
		}
	}

	&__text {
		line-height: 1.3 !important;
		max-width: 200px;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;

		@media (max-width: 782px) {
			max-width: 150px;
		}

		@media (max-width: 600px) {
			max-width: unset;
		}
	}

	a.ab-item {
		display: flex !important;
		line-height: 2 !important;
		align-items: center;
		justify-content: space-between;

		>.dpit-ab-title {
			order: 1;
			display: inline-flex;
			align-items: center;
			gap: 5px;
		}

		>.wp-admin-bar-arrow {
			display: block;
			order: 2;
			line-height: 1 !important;
			margin-left: 10px;

			&::before {
				position: static !important;
			}
		}
	}

	.ab-sub-wrapper {
		align-self: flex-start;
	}



	&--tour {
		float: none !important;
	}

	&--action {
		float: none !important;
	}

	// draft or deactivated tour
	&--not-active:not(:hover):not(.hover) {
		.dpit-ab-title {
			opacity: 0.7;
		}
	}

}