@import '../../../includes/assets/css/consts';

/*Admin top bar*/
#wpadminbar {
	@import './modules/dpit-ab-li';
}

body.wp-admin {
	#wpadminbar {
		@import '../../../admin/assets/css/modules/dpit-ab-li';
	}
}

body:not(.wp-admin) {
	#wpadminbar {
		@import '../../../public/assets/css/modules/dpit-ab-li';
	}
}

@media screen and (min-width: 782px) {
	#wp-admin-bar-edit {
		.dpit-ab-li {

			// show under edit just on mobile admin bar
			display: none !important;
		}
	}
}

